"use client";

import React, { useState } from 'react';

interface PelatihanFormProps {
  onSubmit: (data: PelatihanData) => void;
  onCancel: () => void;
  initialData?: PelatihanData;
  isLoading?: boolean;
}

interface PelatihanData {
  tahun: string;
  nama: string;
  penyelenggara: string;
}

const PelatihanForm: React.FC<PelatihanFormProps> = ({
  onSubmit,
  onCancel,
  initialData,
  isLoading = false
}) => {
  const [formData, setFormData] = useState<PelatihanData>({
    tahun: initialData?.tahun || '',
    nama: initialData?.nama || '',
    penyelenggara: initialData?.penyelenggara || ''
  });

  const [errors, setErrors] = useState<Partial<PelatihanData>>({});

  const validateForm = (): boolean => {
    const newErrors: Partial<PelatihanData> = {};

    if (!formData.tahun.trim()) {
      newErrors.tahun = 'Tahun wajib diisi';
    }

    if (!formData.nama.trim()) {
      newErrors.nama = 'Nama pelatihan wajib diisi';
    }

    if (!formData.penyelenggara.trim()) {
      newErrors.penyelenggara = 'Penyelenggara wajib diisi';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    onSubmit(formData);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name as keyof PelatihanData]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }));
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label htmlFor="tahun" className="block text-sm font-medium text-gray-700 mb-2">
          Tahun *
        </label>
        <input
          type="text"
          id="tahun"
          name="tahun"
          value={formData.tahun}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.tahun ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Contoh: 2023"
          disabled={isLoading}
        />
        {errors.tahun && (
          <p className="mt-1 text-sm text-red-600">{errors.tahun}</p>
        )}
      </div>

      <div>
        <label htmlFor="nama" className="block text-sm font-medium text-gray-700 mb-2">
          Nama Pelatihan *
        </label>
        <input
          type="text"
          id="nama"
          name="nama"
          value={formData.nama}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.nama ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Masukkan nama pelatihan..."
          disabled={isLoading}
        />
        {errors.nama && (
          <p className="mt-1 text-sm text-red-600">{errors.nama}</p>
        )}
      </div>

      <div>
        <label htmlFor="penyelenggara" className="block text-sm font-medium text-gray-700 mb-2">
          Penyelenggara *
        </label>
        <input
          type="text"
          id="penyelenggara"
          name="penyelenggara"
          value={formData.penyelenggara}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.penyelenggara ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Masukkan nama penyelenggara..."
          disabled={isLoading}
        />
        {errors.penyelenggara && (
          <p className="mt-1 text-sm text-red-600">{errors.penyelenggara}</p>
        )}
      </div>

      <div className="flex justify-end space-x-3 pt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 border border-gray-300 rounded-lg hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-500 transition-colors"
          disabled={isLoading}
        >
          Batal
        </button>
        <button
          type="submit"
          className="px-4 py-2 text-sm font-medium text-white bg-red-600 border border-transparent rounded-lg hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          disabled={isLoading}
        >
          {isLoading ? 'Menyimpan...' : (initialData ? 'Perbarui' : 'Tambah')}
        </button>
      </div>
    </form>
  );
};

export default PelatihanForm;
