"use client";

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import LoginPopup from './LoginPopup';
import { auth } from '@/lib/auth';

interface HeaderProps {
  isLoggedIn?: boolean;
  onLogout?: () => void;
  onLoginClick?: () => void;
}

const Header = ({ isLoggedIn = false, onLogout, onLoginClick }: HeaderProps) => {
  const [showLogin, setShowLogin] = useState(false);
  const [loggedIn, setLoggedIn] = useState(isLoggedIn);
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const router = useRouter();

  useEffect(() => {
    // Check login status using auth utility
    setLoggedIn(auth.isLoggedIn());
  }, []);

  useEffect(() => {
    // Listen for storage changes (login/logout from other tabs)
    const handleStorageChange = () => {
      setLoggedIn(auth.isLoggedIn());
    };

    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, []);

  const handleLogout = () => {
    auth.logout();
    setLoggedIn(false);
    if (onLogout) onLogout();
    router.push('/'); // Redirect to homepage or login page
  };

  const handleLoginClick = () => {
    if (onLoginClick) {
      onLoginClick();
    } else {
      setShowLogin(true);
    }
  };

  return (
    <header className="bg-white shadow-md border-b-2 border-red-500">
      <div className="container mx-auto px-4 py-3">
        <div className="flex justify-between items-center">
          {/* Logo and Navigation */}
          <div className="flex items-center space-x-8">
            <div className="flex items-center space-x-3">
              <img 
                src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/6f561152-2181-4b8b-be87-e93d8cf33aa8.png" 
                alt="Logo Fakultas Hukum" 
                className="h-12 w-12"
              />
              <div>
                <h1 className="text-lg font-bold text-red-600 font-roboto">Fakultas Hukum</h1>
                <p className="text-xs text-gray-600">Portal Karier Dosen</p>
              </div>
            </div>
            
            <nav className="hidden md:flex">
              <ul className="flex space-x-6">
                <li>
                  <Link href="/" className="text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Beranda
                  </Link>
                </li>
                <li>
                  <Link href="/tentang" className="text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Tentang
                  </Link>
                </li>
                <li>
                  <Link href="/kontak" className="text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Kontak
                  </Link>
                </li>
                {loggedIn && (
                  <li>
                    <Link href="/dashboard" className="text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                      Dashboard
                    </Link>
                  </li>
                )}
              </ul>
            </nav>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center">
            <button
              onClick={() => setShowMobileMenu(!showMobileMenu)}
              className="text-gray-700 hover:text-red-600"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
          </div>

          {/* Login/Logout Icon */}
          <div className="hidden md:flex items-center">
            {loggedIn ? (
              <button
                onClick={handleLogout}
                className="flex items-center space-x-2 text-red-600 hover:text-red-700 transition-colors"
              >
                <div className="w-8 h-8 bg-red-100 rounded-full flex items-center justify-center">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round">
                    <path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4" />
                    <polyline points="16,17 21,12 16,7" />
                    <line x1="21" y1="12" x2="9" y2="12" />
                  </svg>
                </div>
                <span className="hidden sm:block font-roboto text-sm font-medium">Logout</span>
              </button>
            ) : (
              <button
                onClick={handleLoginClick}
                className="flex items-center space-x-2 text-red-600 hover:text-red-700 transition-colors"
              >
                <div className="w-8 h-8 bg-red-100 rounded-full flex items-center justify-center">
                  <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clipRule="evenodd" />
                  </svg>
                </div>
                <span className="hidden sm:block font-roboto text-sm font-medium">Login</span>
              </button>
            )}
          </div>
        </div>

        {/* Mobile Menu */}
        {showMobileMenu && (
          <div className="md:hidden bg-white border-t border-gray-200">
            <nav className="px-4 py-3">
              <ul className="space-y-2">
                <li>
                  <Link href="/" className="block text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Beranda
                  </Link>
                </li>
                <li>
                  <Link href="/tentang" className="block text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Tentang
                  </Link>
                </li>
                <li>
                  <Link href="/kontak" className="block text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                    Kontak
                  </Link>
                </li>
                {loggedIn && (
                  <li>
                    <Link href="/dashboard" className="block text-gray-700 hover:text-red-600 font-roboto text-sm font-medium transition-colors">
                      Dashboard
                    </Link>
                  </li>
                )}
                <li className="pt-2 border-t border-gray-200">
                  {loggedIn ? (
                    <button
                      onClick={handleLogout}
                      className="flex items-center space-x-2 text-red-600 hover:text-red-700 transition-colors w-full text-left"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round">
                        <path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4" />
                        <polyline points="16,17 21,12 16,7" />
                        <line x1="21" y1="12" x2="9" y2="12" />
                      </svg>
                      <span className="font-roboto text-sm font-medium">Logout</span>
                    </button>
                  ) : (
                    <button
                      onClick={handleLoginClick}
                      className="flex items-center space-x-2 text-red-600 hover:text-red-700 transition-colors w-full text-left"
                    >
                      <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clipRule="evenodd" />
                      </svg>
                      <span className="font-roboto text-sm font-medium">Login</span>
                    </button>
                  )}
                </li>
              </ul>
            </nav>
          </div>
        )}
      </div>
      {showLogin && <LoginPopup onClose={() => setShowLogin(false)} />}
    </header>
  );
};

export default Header;
