"use client";

import React, { useState } from 'react';

interface TugasFormProps {
  onSubmit: (data: TugasData) => void;
  onCancel: () => void;
  initialData?: TugasData;
  isLoading?: boolean;
}

interface TugasData {
  tugas: string;
}

const TugasForm: React.FC<TugasFormProps> = ({
  onSubmit,
  onCancel,
  initialData,
  isLoading = false
}) => {
  const [formData, setFormData] = useState<TugasData>({
    tugas: initialData?.tugas || ''
  });

  const [errors, setErrors] = useState<Partial<TugasData>>({});

  const validateForm = (): boolean => {
    const newErrors: Partial<TugasData> = {};

    if (!formData.tugas.trim()) {
      newErrors.tugas = 'Tugas pokok wajib diisi';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    onSubmit(formData);
  };

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name as keyof TugasData]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }));
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label htmlFor="tugas" className="block text-sm font-medium text-gray-700 mb-2">
          Tugas Pokok *
        </label>
        <textarea
          id="tugas"
          name="tugas"
          value={formData.tugas}
          onChange={handleChange}
          rows={4}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.tugas ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Masukkan tugas pokok..."
          disabled={isLoading}
        />
        {errors.tugas && (
          <p className="mt-1 text-sm text-red-600">{errors.tugas}</p>
        )}
      </div>

      <div className="flex justify-end space-x-3 pt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 border border-gray-300 rounded-lg hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-500 transition-colors"
          disabled={isLoading}
        >
          Batal
        </button>
        <button
          type="submit"
          className="px-4 py-2 text-sm font-medium text-white bg-red-600 border border-transparent rounded-lg hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          disabled={isLoading}
        >
          {isLoading ? 'Menyimpan...' : (initialData ? 'Perbarui' : 'Tambah')}
        </button>
      </div>
    </form>
  );
};

export default TugasForm;
