"use client";

import React, { useState } from 'react';

interface PengalamanFormProps {
  onSubmit: (data: PengalamanData) => void;
  onCancel: () => void;
  initialData?: PengalamanData;
  isLoading?: boolean;
}

interface PengalamanData {
  tahun: string;
  posisi: string;
  unit: string;
}

const PengalamanForm: React.FC<PengalamanFormProps> = ({
  onSubmit,
  onCancel,
  initialData,
  isLoading = false
}) => {
  const [formData, setFormData] = useState<PengalamanData>({
    tahun: initialData?.tahun || '',
    posisi: initialData?.posisi || '',
    unit: initialData?.unit || ''
  });

  const [errors, setErrors] = useState<Partial<PengalamanData>>({});

  const validateForm = (): boolean => {
    const newErrors: Partial<PengalamanData> = {};

    if (!formData.tahun.trim()) {
      newErrors.tahun = 'Tahun wajib diisi';
    }

    if (!formData.posisi.trim()) {
      newErrors.posisi = 'Posisi wajib diisi';
    }

    if (!formData.unit.trim()) {
      newErrors.unit = 'Unit kerja wajib diisi';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    onSubmit(formData);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name as keyof PengalamanData]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }));
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label htmlFor="tahun" className="block text-sm font-medium text-gray-700 mb-2">
          Tahun *
        </label>
        <input
          type="text"
          id="tahun"
          name="tahun"
          value={formData.tahun}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.tahun ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Contoh: 2020-Sekarang"
          disabled={isLoading}
        />
        {errors.tahun && (
          <p className="mt-1 text-sm text-red-600">{errors.tahun}</p>
        )}
      </div>

      <div>
        <label htmlFor="posisi" className="block text-sm font-medium text-gray-700 mb-2">
          Posisi *
        </label>
        <input
          type="text"
          id="posisi"
          name="posisi"
          value={formData.posisi}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.posisi ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Masukkan posisi..."
          disabled={isLoading}
        />
        {errors.posisi && (
          <p className="mt-1 text-sm text-red-600">{errors.posisi}</p>
        )}
      </div>

      <div>
        <label htmlFor="unit" className="block text-sm font-medium text-gray-700 mb-2">
          Unit Kerja *
        </label>
        <input
          type="text"
          id="unit"
          name="unit"
          value={formData.unit}
          onChange={handleChange}
          className={`w-full px-3 py-2 border rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500 ${
            errors.unit ? 'border-red-500' : 'border-gray-300'
          }`}
          placeholder="Masukkan unit kerja..."
          disabled={isLoading}
        />
        {errors.unit && (
          <p className="mt-1 text-sm text-red-600">{errors.unit}</p>
        )}
      </div>

      <div className="flex justify-end space-x-3 pt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 border border-gray-300 rounded-lg hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-500 transition-colors"
          disabled={isLoading}
        >
          Batal
        </button>
        <button
          type="submit"
          className="px-4 py-2 text-sm font-medium text-white bg-red-600 border border-transparent rounded-lg hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          disabled={isLoading}
        >
          {isLoading ? 'Menyimpan...' : (initialData ? 'Perbarui' : 'Tambah')}
        </button>
      </div>
    </form>
  );
};

export default PengalamanForm;
