"use client";

import React, { useState } from "react";
import { auth } from "@/lib/auth";

interface LoginPopupProps {
  onClose: () => void;
}

const LoginPopup: React.FC<LoginPopupProps> = ({ onClose }) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [showForgot, setShowForgot] = useState(false);

  const adminContact = {
    email: "admin@fakultashukum.ac.id",
    phone: "+62-812-3456-7890",
  };

  const handleLogin = () => {
    setError("");
    // Simple mock authentication logic
    if (username === "admin" && password === "admin123") {
      auth.login('admin-token', 'admin');
      window.location.href = "/admin/dashboard";
    } else if (username === "dosen" && password === "dosen123") {
      auth.login('dosen-token', 'dosen');
      window.location.href = "/dosen/dashboard";
    } else {
      setError("Invalid username or password");
    }
  };

  const handleForgotPassword = () => {
    setShowForgot(true);
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-96 shadow-lg relative">
        <button
          className="absolute top-2 right-2 text-gray-600 hover:text-gray-900"
          onClick={onClose}
          aria-label="Close login popup"
        >
          &times;
        </button>
        <h2 className="text-2xl font-bold mb-4 text-center">Login</h2>
        {error && <p className="text-red-600 mb-2">{error}</p>}
        {!showForgot ? (
          <>
            <input
              type="text"
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full border border-gray-300 rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-red-500"
            />
            <input
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full border border-gray-300 rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-red-500"
            />
            <button
              onClick={handleLogin}
              className="w-full bg-red-600 text-white py-2 rounded hover:bg-red-700 transition"
            >
              Login
            </button>
            <p
              className="mt-3 text-sm text-blue-600 cursor-pointer hover:underline text-center"
              onClick={handleForgotPassword}
            >
              Forgot Password?
            </p>
          </>
        ) : (
          <div className="text-center">
            <p className="mb-2">
              Please contact admin to reset your password:
            </p>
            <p>Email: <a href={`mailto:${adminContact.email}`} className="text-blue-600 hover:underline">{adminContact.email}</a></p>
            <p>Phone: <a href={`tel:${adminContact.phone}`} className="text-blue-600 hover:underline">{adminContact.phone}</a></p>
            <button
              onClick={() => setShowForgot(false)}
              className="mt-4 bg-gray-300 px-4 py-2 rounded hover:bg-gray-400 transition"
            >
              Back to Login
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default LoginPopup;
